/* Forms & Input Fields */

.input-field() {
	position: relative;
	display: block;
	width: 100%;
	padding: .5em 1em;
	background: var(--input-background-color);
	color: var(--default-text-color);
	border: 1px solid var(--default-border-color);
	border-radius: var(--input-border-radius);
}

fieldset {
	border: 1px solid var(--default-border-color);
	border-radius: var(--border-radius);
}

input, button, select, textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
	outline: 0;
}

input[type="number"] {
	text-align: end;
}

input[type="checkbox"] {
	-webkit-appearance: none;
	position: relative;
	background: var(--input-background-color);
	height: 1.25em;
	width: 1.25em;
	border: 1px solid var(--default-border-color);
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
	border-radius: .25em;
	vertical-align: text-bottom;
	margin-inline-end: .5em;
	&:active, &:checked:active {
		box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px 1px 3px rgba(0, 0, 0, 0.1);
	}
	&:checked {
		background-color: #999;
		background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><path fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='M6 10l3 3l6-6'/></svg>");
	}
	&:hover {
		box-shadow: 0 0 0 0.25rem #ccc;
	}
}

input[type="radio"] {
	display: inline-block;
	position: relative;
	-webkit-appearance: none;
	background: var(--input-background-color);
	height: 1.25em;
	width: 1.25em;
	border: 1px solid var(--default-border-color);
	border-radius: 50%;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
	vertical-align: text-bottom;
	margin-inline-end: .5em;
	&:active, &:checked:active {
		box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px 1px 3px rgba(0, 0, 0, 0.1);
	}
	&:checked {
		background-color: #999;
		background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2' fill='%23fff'/></svg>");
	}
	&:hover {
		box-shadow: 0 0 0 0.25rem #ccc;
	}
}

input[type="range"] {
	padding-left: 0;
	padding-right: 0;
	margin-left: var(--input-padding-x);
	margin-right: var(--input-padding-x);
}

.form-group {
	&.required > label::after {
		content: ' *';
		color: #f00;
	}
	&:not([class*="col-"]) {
		margin-bottom: var(--gutter-y);
	}
	> label {
		display: block;
		padding-bottom: var(--input-padding-y);
	}
}

.form-input {
	.input-field();
	display: block;
	width: 100%;
	min-width: 1px; // Firefox fix
	padding: var(--input-padding-y) var(--input-padding-x);
	transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
	transition: all 50ms;
	cursor: default;

	&[type="color"] {
		height: 37px;
	}
	&[type="file"] {
		font-size: 12px;
	}
	&[disabled] {
		opacity: 0.5;
	}
	&[readonly] {
		color: #999;
	}

	.dropdown-menu li label:hover {
		background: var(--default-background-color);
	}
}

textarea.form-input {
	padding: var(--input-padding-x);
}

.form-select {
	.input-field();
	background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
	background-repeat: no-repeat;
	background-position: right 1em center;
	background-size: 16px 12px;
	padding-inline-end: 2.5em;
	appearance: none;
	cursor: default;
	user-select: none;
}

.dropdown .form-select + .dropdown-menu {
	.option:has(input[type="checkbox"]:checked)
	.option:has(input[type="radio"]:checked) {
		font-weight: bold;
	}
}

.form-switch label {
  position: relative;
	display: inline-block;
  background: #ccc;
  border-radius: 1em;
  width: 4em;
  height: 2em;
  transition: background 0.25s;
	&::before {
		content: "";
		display: inline-block;
		background: #fff;
		border-radius: 50%;
		width: 1.45em;
		height: 1.45em;
		position: absolute;
		top: .25em;
		left: .25em;
		transition: left 0.25s;
	}
	&:has(:checked) {
		background: #2196f3;
		&::before {
			left: 2.25em;
		}
	}
}

.form-toggle {
	display: flex;
	flex-direction: row;
	padding: .25em;
	gap: .25em;
	background: #d5d5db;
	color: #fff;
	border-radius: var(--border-radius);

	label {
		flex: 1 1 auto;
		display: inline-block;
		border-radius: var(--border-radius);
		padding: var(--input-padding-y) var(--input-padding-x);
		text-align: center;
		&:has(:checked) {
			background: #fff;
			color: #000;
		}
		&:hover:not(:has(:checked)):not(:has(:disabled)) {
			background: #fff3;
			cursor: pointer;
		}
		&:has(:focus) {
			outline: 0;
			border-color: var(--input-border-color-focus);
		}
		&:has(:disabled) {
			opacity: .5;
		}
	}
}

.input-unstyled {
	margin: 0;
	padding: 0;
	border: none;
	background: none;
	color: inherit;
}

.checkbox, .radio {
	padding: var(--input-padding-y) 0;
}

.input-group {
	.input-field();
	display: flex;
	padding: 0;
	.form-input {
		flex: 1 1 auto;
		background-color: unset;
		border: none;
		border-radius: 0;
		box-shadow: none;
	}
	.input-group-text {
		flex: 0 1 auto;
		margin: 0 -1px;
		padding: var(--input-padding-y) var(--input-padding-x);
		background-color: var(--default-background-color);
		border-inline-start: 1px solid var(--default-border-color);
		border-inline-end: 1px solid var(--default-border-color);
		color: var(--default-text-color);
		white-space: nowrap;
		font-weight: normal;
		text-align: center;
	}
	.input-group-icon {
		flex: 0 1 auto;
		padding: var(--input-padding-y) var(--input-padding-x);
		padding-inline-end: 0;
		align-self: center;
	}
	> .btn {
		margin: -1px;
	}
	+ .input-group {
		margin-top: -1px;
	}
	.form-input:first-child, .btn:first-child, .input-group-btn:first-child .btn,
	.input-group-text:first-child {
		border-radius: var(--input-border-radius) 0 0 var(--input-border-radius);
	}
	.form-input:last-child, .input-group-btn:last-child .btn,
	.input-group-text:last-child {
		margin-inline-end: -1px;
		border-end-end-radius: var(--input-border-radius);
		border-start-end-radius: var(--input-border-radius);
	}
}

fieldset[disabled] > :not(legend),
:input[disabled],
.form-input[disabled],
.form-select[disabled] {
	opacity: .5;
}

input[data-toggle="tags"] + .form-input {
	.tokens {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		list-style: none;
		padding: 0;
		margin: -.5em;
		cursor: text;
	}
	.tag {
		background: rgba(0, 93, 152, 0.2);
		padding: 0.25em 0.5em;
		margin: .25em;
		border-radius: .25em;
		cursor: default;
		.remove {
			background: rgba(255, 15, 15, 0.4);
			color: #fff;
			border-radius: .25em;
			padding: 0 .25em;
			cursor: pointer;
			&:hover {
				background: rgba(255, 15, 15, 0.6);
			}
		}
	}
	.input {
		flex: 1 1 auto;
		background: transparent;
		border: none;
		outline: none;
		padding: .5em .5em;
	}
}